import React from 'react';

const Card = ({name, email,id}) => {
	return(
		<div className ='tc  bg-light-green tw grow mr2 w5 br2 pa3 mt4 dib bw2 shadow-5'>
			<img src={`https://robohash.org/${id}?200x200`} alt ="robos" />
			<div>
				<h2>{name}</h2>
				<p>{email}</p>
			</div>
		</div>
		);
}
export default Card;